import React, { Fragment } from 'react';
import { Switch, Route } from 'react-router-dom';
import Dashboard from './Dashboard';
import Login from "./Login";

export default function Admin(props) {
    const { rootData } = props;
    // setting background of admin pages
    document.body.style.background = null;
    return (
        <Fragment>
                <Switch>
                    <Route path="/admin" component={Dashboard} />
                    <Route exact path="/login" render={props => <Login rootData={rootData} {...props} />} />
                    <Route render={() => <h1 className="bg-dark text-center text-light p-4">Missing admin route!!</h1>} />
                </Switch>
        </Fragment>
    )
}