import React from 'react';
import classNames from 'classnames';
import { Container } from 'reactstrap';
import Header from './Header';
import { Switch, Route } from 'react-router-dom';
import {State} from "../";
export default props => (
    <Container fluid className={classNames('content', {'is-open': props.isOpen})}>
      <Header toggle={props.toggle}/>
      <div className="body">
      <Switch>
        <Route exact path={props.match.path} component={State} />
        <Route exact path={props.match.path + "/Home-1"} component={() => "Home-1" } />
        <Route exact path={props.match.path + "/state"} component={State} />
        <Route render={() => <h1 className="bg-dark text-center text-light p-4">Missing admin content route!!</h1>} />
      </Switch>
      </div>
    </Container>
)